import React, { Component } from 'react';
import './Grid.css';
import GridItem from "./GridItem/GridItem";

class Grid extends Component {

  constructor(props) {
    super(props);
    this.array = Array(11).fill(0).map((it, i) => i + 1);
    this.generateImages();
  }

  generateImages() {
    setInterval(() => {
      this.array = this.array.sort((a, b) => Math.random() * (-1));
      console.log(this.array);
    }, 2000);
  }

  render() {
    return (
      <section className="Grid" id="photo">
        <div className="Images">
          {this.array.map((it, i) => <GridItem id={it} key={i}/>)}
          <GridItem id="1"/>
        </div>
      </section>
    )
  }
}

export default Grid;
