import React, {Component} from 'react';
import './Parallax.css';

class Parallax extends Component {
  render() {
    return (
      <section className="Parallax" style={{backgroundImage: `radial-gradient(at center, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.9)), url('${this.props.background}')`}}>
        <h2 className="Parallax__title">{this.props.title}</h2>
      </section>
    )
  }
}

export default Parallax;
